*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'montseratt', sans-serif;

}

.app.warm{
  background-image: url(./images/warm.bg.jpeg);
}

.app{
  background-image: url(./images/cold.bg.jpeg);
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease-out;
}

main{
  min-height: 100vh;
  background-image: linear-gradient(to bottom,rgba(0,0,0,0.2),rgba(0,0,0,0.75));
  padding: 25px;
}

.search-box{
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar{
  padding: 15px;
  display: block;
  width: 100%;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color:rgba(255,255,255,0.75);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;

  box-shadow: 0px 5px rgba(0,0,0,0.2);
  color: #313131;
  font-size: 20px;
  transition: 0.4s ease; 
}

.search-box .search-bar:focus{
  background-color: rgba(255,255,255,0.75);
}

.location-box{
  text-align: center;
  color: #fff;
  font-weight: 500;
  font-style: italic;
  font-size: 20px;
  text-shadow: 2px 2px rgba(50,50,70,0.5);
}

.weather-box{
  text-align: center;
}

.weather-box .temp{
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color:rgba(255,255,255,0.2);
  border-radius: 16px;
  padding: 15px 25px;
  color: #fff;
  font-size: 102px;
  font-weight: 900;
  text-shadow: 3px 6px rgba(50,50,70,0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0,0,0,0.2);
}

.weather-box .weather{
  color: #fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
}



